<template>
  <div class="wrapper">

    <i class="iconfont icon-shanxingtu" @click="el"></i>
    <i class="iconfont icon-zhixiang"></i>
    <div class="mounthText">Mounth</div>
    <div class="item-bg"></div>
    <div class="news-slider">
      <div class="news-slider__wrp swiper-wrapper">
        <div class="news-slider__item swiper-slide" v-for="item,i in $store.state.dataList" :key="i">
          <div class="news__item">
            <div class="news-date">
              <span class="news-date__title">{{item.year}}</span>
              <span class="news-date__txt">{{item.month}}月</span>
            </div>
            <div class="news__title">
              {{item.title}}
            </div>

            <p class="news__txt">
              {{item.content}}
            </p>

            <div class="news__img">
              <img v-if="item.img==''" :src='image' alt="news">
              <img :src='item.img' alt="news">
            </div>
          </div>
        </div>

      </div>

      <div class="news-slider__ctr">

        <div class="news-slider__arrows">
          <button class="news-slider__arrow news-slider-prev">
            <span class="icon-font">
              <i class="iconfont icon-jiantou1"></i>
            </span>
          </button>
          <button class="news-slider__arrow news-slider-next">
            <span class="icon-font">
              <i class="iconfont icon-jiantou1 right"></i>
            </span>
          </button>
        </div>

        <div class="news-slider__pagination"></div>

      </div>

    </div>

  </div>
</template>

<script>
import Swiper from '../assets/js/swiper.min.js'
import eventBus from '../eventBus';
export default {
  data () {
    return {

    }
  },
  methods: {
    el () {
      this.$store.commit('cahngeShowMenu', true)
    },
    randomFrom (lowerValue, upperValue) {
      return Math.floor(Math.random() * (upperValue - lowerValue + 1) + lowerValue);
    },
    inputContent (item) {
      console.log(888);
      const h = this.$createElement;
      this.$msgbox({
        title: `${item.year}年${item.month}月${item.day}日`,
        message: h('p', null, [
          h('p', { style: 'color: teal' }, `${item.content}`)
        ]),
        showCancelButton: true,
        showConfirmButton: false,
        beforeClose: (action, instance, done) => {
          done();
        }
      })
    },
    // fun_str (item) {
    //   return JSON.stringify(item);
    // }

  },
  mounted () {
    // console.log(this.$store.inputContent);
    // let _this = this
    // this.$store.dispatch('render')

    var _this = this
    var bg = document.querySelector('.item-bg');
    var items = document.querySelectorAll('.news__item');
    var item = document.querySelector('.news__item');
    function cLog (content) {
      console.log(content)
    }

    if ($(window).width() > 800) {
      $(document).on("mouseover", ".news__item", function (_event, _element) {

        var newsItem = document.querySelectorAll('.news__item');
        newsItem.forEach(function (element, index) {
          element.addEventListener('mouseover', function () {
            var x = this.getBoundingClientRect().left;
            var y = this.getBoundingClientRect().top;
            var width = this.getBoundingClientRect().width;
            var height = this.getBoundingClientRect().height;

            $('.item-bg').addClass('active');
            $('.news__item').removeClass('active');
            // $('.news__item').removeClass('active');


            bg.style.width = width + 'px';
            bg.style.height = height + 'px';
            bg.style.transform = 'translateX(' + x + 'px ) translateY(' + y + 'px)';
          });

          element.addEventListener('mouseleave', function () {
            $('.item-bg').removeClass('active');
            $('.news__item').removeClass('active');
          });

        });

      });
    }
    var swiper = new Swiper('.news-slider', {
      effect: 'coverflow',
      grabCursor: true,
      loop: true,
      centeredSlides: true,
      keyboard: true,
      spaceBetween: 0,
      slidesPerView: 'auto',
      speed: 300,
      coverflowEffect: {
        rotate: 0,
        stretch: 0,
        depth: 0,
        modifier: 3,
        slideShadows: false
      },
      breakpoints: {
        480: {
          spaceBetween: 0,
          centeredSlides: true
        }
      },
      simulateTouch: true,
      navigation: {
        nextEl: '.news-slider-next',
        prevEl: '.news-slider-prev'
      },
      pagination: {
        el: '.news-slider__pagination',
        clickable: true
      },
      on: {
        init: function () {
          var activeItem = document.querySelector('.swiper-slide-active');

          var sliderItem = activeItem.querySelector('.news__item');

          $('.swiper-slide-active .news__item').addClass('active');

          var x = sliderItem.getBoundingClientRect().left;
          var y = sliderItem.getBoundingClientRect().top;
          var width = sliderItem.getBoundingClientRect().width;
          var height = sliderItem.getBoundingClientRect().height;


          $('.item-bg').addClass('active');

          bg.style.width = width + 'px';
          bg.style.height = height + 'px';
          bg.style.transform = 'translateX(' + x + 'px ) translateY(' + y + 'px)';
        },
        click: function (e) {
          // console.log(e.target.className);
          // console.log(e.target.nodeName);
          let target = e.target.className
          if (target == 'news__txt' || e.target.nodeName == 'IMG' || target == 'news - date' || target == 'news__item active' || target == 'news__item' || target == 'news__title' || target == 'news - date__title' || target == 'news - date__txt') {
            // console.log(this.el.innerText);
            // let item = JSON.parse(this.clickedSlide.attributes["data-href"])
            // _this.fun_str()
            let index = this.clickedSlide.attributes['data-swiper-slide-index'].nodeValue
            let item = _this.$store.state.dataList[index]
            _this.inputContent(item)
          }
        }
      }
    });
    swiper.on('touchEnd', function () {
      $('.news__item').removeClass('active');
      $('.swiper-slide-active .news__item').addClass('active');
    });

    swiper.on('slideChange', function () {
      $('.news__item').removeClass('active');
    });

    swiper.on('slideChangeTransitionEnd', function () {
      $('.news__item').removeClass('active');
      var activeItem = document.querySelector('.swiper-slide-active');

      var sliderItem = activeItem.querySelector('.news__item');

      $('.swiper-slide-active .news__item').addClass('active');

      var x = sliderItem.getBoundingClientRect().left;
      var y = sliderItem.getBoundingClientRect().top;
      var width = sliderItem.getBoundingClientRect().width;
      var height = sliderItem.getBoundingClientRect().height;
      $('.item-bg').addClass('active');
      bg.style.width = width + 'px';
      bg.style.height = height + 'px';
      bg.style.transform = 'translateX(' + x + 'px ) translateY(' + y + 'px)';
    });
  },

}
</script>
<style>
    @import url("../assets/css/swiper.min.css");
    @import url("../assets/css/style.css");
    @import url("//at.alicdn.com/t/font_2516563_dwm6jc2uko8.css");
    
    .icon-jiantou1:before {
      content: "\e699";
      font-size: 20px;
    }
    .news-slider__arrow.news-slider-next {
      right: 15px;
      /* transform: rotate(180deg) */
      transform: translateY(-50%) rotate(180deg);
    }
    .icon-shanxingtu{
      position: absolute;
      font-size: 43px;
      color: white;
      transform: translate(30px,30px);
      transition: 0.2s;
      cursor: pointer;
    }
    .icon-shanxingtu:hover{
      font-size: 50px;
    }
    .icon-zhixiang{
      position: absolute;
      font-size: 55px;
    color: white;
    transform: translate(32px,561px);
    }
    .mounthText{
      position: absolute;
      color: white;
      left: 25px;
      font-size: 20px;
      top: 131px;
    }
    /* .wrapper{
      height: 100vh;
    } */
</style>