<template>
    <div class="age">
        <h1>{{ageNum}}</h1>
    </div>
</template>

<script>
import eventBus from "../eventBus"
export default {
    data(){
        return {
            ageNum:''
        }
    },
    mounted(){
        let onThis = this
        eventBus.$on('ageChange',function(year,day,month){
            let myDate = new Date()
            let nowDay = myDate.getDate()
            let nowMonth = myDate.getMonth() + 1
            let nowYear = myDate.getFullYear()
            if(month<nowMonth){
                onThis.ageNum = `You are ${nowYear - year} years old` 
            }else if(month>nowMonth&&year<nowYear){
                onThis.ageNum = `You are ${nowYear - year - 1} years old` 
            }else if(month == nowMonth){
                if(day < nowDay){
                    onThis.ageNum = `You are ${nowYear - year} years old`
                }else if(day > nowDay&&year<nowYear){
                    onThis.ageNum = `You are ${nowYear - year - 1} years old` 
                }else if(day > nowDay&&year==nowYear){
                    onThis.ageNum =`You weren't born yet` 
                }else if(day==nowDay){
                    let birthday = nowYear - year
                    onThis.ageNum = `祝你${birthday}岁生日快乐!!!`
                }
            }else if(year==nowYear&&month>nowMonth){
                onThis.ageNum =`You weren't born yet` 
            }
        })
    },
}
</script>
<style>
    .age{
        width: 900px;
        height: 600px;
        display: flex;
        align-items: center;
        margin: 0 auto;
        justify-content: center;
    }
    h1{
        color: white;
        font-size: 80px;
    }
</style>