<template>
  <div class="home">
    <swiperHistory ref="mySwiper" v-if="$store.state.clock"></swiperHistory>
    <div class="dayNum">{{$store.state.day}}</div>
    <div class="xiexian"></div>
    <div class="mounNum">{{$store.state.mounth}}</div>
    <div class="block">
      <span class="demonstration"></span>
      <el-slider v-loading.fullscreen.lock="$store.state.fullscreenLoading" @change="reSlider" v-model="value" :step="1" :min="1" :max="31" show-stops>
      </el-slider>
    </div>
    <!-- //侧边弹出框 -->
    <transition name="el-fade-in-linear">
      <menuModel v-show="$store.state.showMenu"></menuModel>
    </transition>

    <div class="dayText">Day</div>
  </div>
</template>
<script>
// // @ is an alias to /src

import swiperHistory from '../components/swiperHistory'
import eventBus from '../eventBus';
import menuModel from '../components/menuModel.vue'
export default {
  name: "Home",
  data () {
    return {
      value: 1,
      trim: null,

    }
  },
  components: { swiperHistory, menuModel },
  computed: {
    swiper () {
      return this.$refs.mySwiper.$swiper
    },
  },
  methods: {
    reSlider () {
      clearTimeout(this.trim)
      this.trim = setTimeout(() => {
        let goDay = this.value.toString()
        let goMonth = this.$store.state.mounth.toString()
        console.log(goDay, goMonth);
        this.$store.commit('addDay', goDay)
        this.$store.commit('addMounth', goMonth)
        this.$store.state.clock = false
        this.$store.state.fullscreenLoading = true
        //goMonth，goDay命名要和请求的相同
        this.$store.dispatch('forAjax')
        // eventBus.$emit('reRender')

        // this.$nextTick(() => {
        //   // 在 DOM 中添加 my-component 组件
        //   this.clock = true;
        // });
      }, 700)
    },
    // showLoad () {

    // }
  },
  mounted () {
    if (this.$store.state.day == '') {
      this.value = 1
    } else {
      this.value = Number(this.$store.state.day)
    }

    eventBus.$on('nowHome', () => {
      this.value = Number(this.$store.state.day)
    })

    let bigImgFav = 'assets/img/Ui变/大盘子/大盘.png'
    this.$store.state.bigImgUrl = require('@/' + bigImgFav)
  },



};
</script>
