import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import store from '../store/index'
// import ElementUI from 'element-ui';
// import 'element-ui/lib/theme-chalk/index.css';
// Vue.use(ElementUI)
Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/',
  //   name: 'first',
  //   component: () => import(/* webpackChunkName: "about" */ '../App.vue')
  // },
  {
    path: '/home',
    name: 'Home',
    component: () => import(/* webpackChunkName: "about" */ '../views/Home.vue')
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/donate',
    name: 'donate',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/donate.vue')
  },
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  //使用router获取this
  // console.log(router.app.$loading);
  console.log(to);
  if (to.path == '/') {
    store.commit("showBox");
  }
  if (to.path == '/home') {

    // console.log(store.state.day);
    // console.log(store.state.mounth);
    if (store.state.day == '') {
      const loading = router.app.$loading({
        lock: true,
        text: 'Loading',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      store.state.clock = false
      let date = new Date()
      let nowDay = date.getDate().toString()
      let nowMounth = (date.getMonth() + 1).toString();
      store.commit('defaultDay', nowDay)
      store.commit('defaultMounth', nowMounth)
      store.dispatch('forAjax').then(res => {
        // loading.close()
        setTimeout(() => {
          loading.close()
        }, 500)
      })
      // console.log(nowMounth, nowDay);
    }
  }
  if (to.path == '/about') {

  }
  next()
})

export default router
