<template>
  <div>
    <!-- 遮罩 -->
    <div class="bigBoxBg" v-if="showModal"></div>

    <div class="modal">
      <div class="modal-header">
        <h3 class="title">Date of Birth</h3>
      </div>
      <div class="line"></div>
      <div class="modal-content">
        <div class="left">
          <div>Day</div>
          <input type="text" v-model="day" :class="changeBorder==1" @focus="inputBorderChange" />
        </div>
        <div class="center">
          <div>Month</div>
          <input type="text" v-model="month" />
        </div>
        <div class="right">
          <div>Year</div>
          <input type="text" v-model="year" />
        </div>
      </div>
      <div class="checkBox">
        <div class="radius">
          <div @click="isshow = !isshow" class="check" :class="{active:isshow==true}"></div>
          <div @click="isshow = !isshow" class="text">without birthday</div>
        </div>
        <div class="warn" v-if="shakeBox">
          <div>Please enter the correct date!!!</div>
        </div>
      </div>

      <div class="modal-footer">
        <button class="showTime" :class='{active:showTime==true,shake:shakeBox}' @click="submit">Showtime</button>
        <button class="Cancel" @click="Cancel">Cancel</button>
      </div>
    </div>
  </div>
</template>

<script>
import eventBus from "../eventBus"
// import axios from 'axios'
export default {
  data: function () {
    return {
      showModal: true,
      day: '',
      month: '',
      year: '',
      isshow: false,
      showTime: 'false',
      changeBorder: 'false',
      shakeBox: false,
      lock: true
    };
  },
  methods: {
    Cancel: function () {
      // this.showModal = false
      this.$emit('changeModel')
      if (this.$route.path != '/home') {
        this.$router.push('./home')
      }
    },
    submit () {
      //判断输入日期格式
      if (this.day == "" || isNaN(this.day) || this.month == "" || isNaN(this.month) || this.year == "" || isNaN(this.year) || this.day < 0 || this.day > 31 || this.month < 0 || this.month > 12 || this.year < 0 || this.year > 2021 || this.year < 1900) {
        this.shakeBox = true
        setTimeout(() => { this.shakeBox = false }, 1300)
        return
      }
      this.showTime = true
      let onthis = this
      this.$store.commit('addDay', this.day)
      this.$store.commit('addMounth', this.month)
      this.$store.state.clock = false
      //goMonth，goDay命名要和请求的相同
      // console.log(goMonth, goDay);
      this.$store.dispatch('forAjax')
      setTimeout(function () {
        onthis.showTime = false
        onthis.$emit('changeModel')
        if (onthis.$route.path == '/home') {
          eventBus.$emit('nowHome')
          return
        }
        if (onthis.isshow == false) {
          onthis.$emit('changeShowAge')
          eventBus.$emit('ageChange', onthis.year, onthis.day, onthis.month)

        }
        if (onthis.isshow == true) {
          onthis.$router.push('/home')
        }
      }, 100)
    },
    inputBorderChange () {
      this.changeBorder = true
    }
  },
  mounted () {
  },
};
</script>
<style>
.bigBoxBg {
  position: absolute;
  z-index: 5;
  width: 100%;
  height: 100vh;
  top: 0;
  bottom: 0;
  left: 0;
  opacity: 0.2;
  background: #000;
}
.modal {
  text-align: center;
  position: absolute;
  z-index: 6;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 580px;
  height: 290px;
  border-radius: 300px;
  border: 6px solid black;
  background: white;
}
.modal .title {
  font-size: 30px;
  font-weight: 800;
  margin: 20px 0 10px 0;
}
.modal .line {
  display: flex;
  /* justify-content: center; */
  width: 80%;
  height: 5px;
  background: #000;
  margin: 0 auto;
}
.modal .modal-content {
  width: 450px;
  /* border: 1px solid red; */
  margin: 40px auto 10px auto;
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  font-weight: 500;
}
.modal .modal-content input {
  width: 80px;
  border: 3px solid black;
  border-radius: 5px;
  outline: none;
}

.modal .modal-content input.active {
  border: 3px solid red;
}
.checkBox {
  /* border: 1px red solid; */
  display: flex;
  align-items: center;
  text-align: left;
  padding-left: 63px;
  margin-top: 20px;
}
.checkBox .radius {
  display: flex;
  align-items: center;
}
.warn{
  color: red;
  font-size: 13px;
  margin-left: 23px;
}
.checkBox .radius .check {
  cursor: pointer;
  width: 12px;
  height: 12px;
  /* background: #000; */
  border-radius: 50%;
  border: 3px solid rgb(139, 139, 139);
  margin-right: 6px;
}
.checkBox .radius .check.active {
  background: rgb(53, 53, 53);
}
.checkBox .radius .text {
  user-select: none;
  cursor: pointer;
  font-size: 8px;
  color: rgb(139, 139, 139);
}
.modal-footer {
  width: 500px;
  /* border: 1px red solid; */
  text-align: end;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-top: 10px;
}
.modal-footer .showTime {
  cursor: pointer;
  width: 130px;
  height: 40px;
  border: 3px solid black;
  background: rgb(255, 138, 70);
  border-radius: 10px;
  font-weight: 800;
  font-size: 18px;
  outline: none;
}
.modal-footer .showTime.shake{
  animation: shake 800ms ease-in-out;
}
@keyframes shake {
  10%, 90% { transform: translate3d(-1px, 0, 0); }

  20%, 80% { transform: translate3d(+2px, 0, 0); }

  30%, 70% { transform: translate3d(-4px, 0, 0); }

  40%, 60% { transform: translate3d(+4px, 0, 0); }

  50% { transform: translate3d(-4px, 0, 0); }

}
.modal-footer .showTime.active{
  background: rgb(173, 96, 52);
}
.modal-footer .Cancel {
  cursor: pointer;
  width: 80px;
  height: 30px;
  border: 3px solid black;
  background: rgb(151, 151, 151);
  font-weight: 600;
  margin-left: 62px;
  border-radius: 6px;
  font-size: 18px;
  margin-top: 3px;
  outline: none;
}
</style>
