import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import Swiper from '../assets/js/swiper.min.js'
Vue.use(Vuex)
export default new Vuex.Store({
    state: {
        bigImgUrl: '',
        ishowInputBox: false,
        day: '',
        mounth: '',
        dataList: [],
        clock: true,
        fullscreenLoading: false,
        showMenu: false,
        inputContent: ''
        // showmodel:true
    },

    getters: {

    },
    mutations: {
        increment (state, n) {
            state.ishowInputBox = false
        },
        showBox (state, n) {
            state.ishowInputBox = true
        },
        addDay (state, n) {
            state.day = n
        },
        addMounth (state, n) {
            state.mounth = n
        },
        addDataList (state, n) {

            state.dataList = n
        },
        //路由如果day和mounth为空，设置默认值
        defaultDay (state, n) {
            console.log(n);
            state.day = n
        },
        defaultMounth (state, n) {
            state.mounth = n
        },
        cahngeShowMenu (state, n) {
            state.showMenu = n
        },
        // clearList (state, n) {
        //     state.dataList = []
        // }
    },
    actions: {
        forAjax ({ commit, state }) {
            // console.log(888);
            console.log(state.mounth);
            console.log(state.day);
            // console.log(goMonth, goDay);
            let urlHttp = `https://ali-todayhistory.showapi.com/today-of-history?date=${state.mounth = state.mounth.length < 2 ? '0' + state.mounth : state.mounth}${state.day = state.day.length < 2 ? '0' + state.day : state.day}&needContent=1`
            let config = {
                headers: {
                    Authorization: 'APPCODE 99595f8d42d24f8a8ba62e9854fafc5a'
                }
            }
            axios.get(urlHttp, config).then((res) => {
                function randomFrom (lowerValue, upperValue) {
                    return Math.floor(Math.random() * (upperValue - lowerValue + 1) + lowerValue);
                }
                let dataList = res.data.showapi_res_body.list
                // let dataList = res.data
                console.log(res);
                for (let i = 0; i < dataList.length; i++) {
                    if (dataList[i].img == '') {
                        let n = randomFrom(0, 12)
                        // console.log(n);
                        dataList[i].img = require('../assets/img/' + n + '.jpg')
                    }
                }
                // commit('clearList')
                commit('addDataList', dataList)
                // console.log(state.dataList);
            }).then(() => {
                setTimeout(() => {
                    state.fullscreenLoading = false
                }, 500)
                state.clock = true
            })

        },

    }
})